:root {
  --white: #fff;
  --black: #000;
  --off-white: #f6f6fe;
  --off-black: #494c5f;
  --gradient: linear-gradient(135deg, #a3a8f0, #696fdd);
}

*, :before, :after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.billing__toggle {
  --s: calc(2rem - 8px);
  --p: 4px;
  height: var(--s);
  padding: var(--p);
  box-sizing: content-box;
  aspect-ratio: 2;
  border-radius: var(--s);
  background: radial-gradient(farthest-side, #fff 90%, #0000) left / var(--s) 100% content-box no-repeat, var(--gradient);
  appearance: none;
  cursor: pointer;
  margin: 0;
  transition: all .3s;
}

.billing__toggle:hover {
  opacity: .75;
}

.billing__toggle:checked {
  background-position: 100%;
}

.billing {
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.billing__period {
  transition: opacity .3s;
}

[data-billing="monthly"] .billing__period--annually, [data-billing="annually"] .billing__period--monthly {
  opacity: .5;
}

.tiers {
  grid-template-columns: repeat(3, 350px);
  align-items: center;
  gap: 0;
  display: grid;
}

@media screen and (max-width: 1096px) {
  .tiers {
    display: block;
  }
}

.tier {
  background-color: var(--white);
  text-align: center;
  width: min(350px, 100%);
  border-radius: .5rem;
  padding: 2rem;
  box-shadow: .5rem 0 1.5rem #494c5f33;
}

.tier:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.tier:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.tier:nth-child(2) {
  background-image: var(--gradient);
  color: var(--white);
  padding-block: 3.5rem;
}

.tier:nth-child(2) .tier__perks-item {
  box-shadow: 0 1px #fff3;
}

.tier:nth-child(2) .tier__perks-item:first-child {
  box-shadow: 0 1px #fff3, 0 -1px #fff3;
}

.tier:nth-child(2) .tier__more {
  --color: #696fdd;
  --background: var(--white);
}

@media screen and (max-width: 1096px) {
  .tier {
    margin-inline: auto;
  }

  .tier:first-child, .tier:last-child {
    border-radius: .5rem;
  }

  .tier + .tier {
    margin-top: 1.5rem;
  }
}

.tier__title {
  opacity: .75;
  margin-bottom: 2.5rem;
  font-size: 15px;
}

.tier__price {
  white-space: nowrap;
  font-size: clamp(48px, 16vw, 72px);
  line-height: 1;
  display: none;
}

.tier__price:before {
  content: "$Â ";
  vertical-align: top;
  font-size: clamp(24px, 8.88889vw, 40px);
  line-height: clamp(48px, 16vw, 72px);
  display: inline-block;
}

[data-billing="monthly"] .tier__price--monthly, [data-billing="annually"] .tier__price--annually {
  display: block;
}

.tier__perks {
  margin-block: 3rem 2rem;
  padding: 0;
  list-style-type: none;
}

.tier__perks-item {
  opacity: .75;
  padding: 1.25rem;
  box-shadow: 0 1px #494c5f33;
}

.tier__perks-item:first-child {
  box-shadow: 0 1px #494c5f33, 0 -1px #494c5f33;
}

.tier__more {
  --color: var(--white);
  --background: var(--gradient);
  background: var(--background);
  color: var(--color);
  text-transform: uppercase;
  letter-spacing: .1em;
  cursor: pointer;
  border-radius: .5rem;
  padding: .5rem;
  font-size: 13px;
  line-height: 1.75rem;
  transition: all .2s;
  display: block;
  box-shadow: 0 .5rem .5rem #494c5f1a;
}

.tier__more:hover {
  transform: translateY(.125rem);
  box-shadow: 0 .25rem .2rem #494c5f1a;
}

main {
  --container: 1680px;
  padding: 0 max(1.5rem, 50vw - var(--container) / 2);
}

.section__title {
  text-align: center;
  margin-bottom: 3rem;
  font-size: 2rem;
}

.section__billing {
