%container {
  --container: 1680px;
  padding: 0 max(1.5rem, 50vw - var(--container) / 2);

  &-bleed-left {
    padding-right: max(1.5rem, 50vw - var(--container) / 2);
  }

  &-bleed-right {
    padding-left: max(1.5rem, 50vw - var(--container) / 2);
  }
}

%sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}
